export default {
  components:{
  },
    data(){
        return{
        perPage: 10,
        currentPage: 1,        
        fields: [
        {
          key: 'sid',
          label: 'รหัสนักศึกษา',
          sortable: true,
        },
        {
          key: 'name',
          label: 'ชื่อเล่น',
        },
        {
          key: 'coin',
          label: 'Coin',
          sortable: true,
        },
        {
            key:'uid',
            label: '*',
        }],items:[],
        item:[],
        search:'',
        search2:'',
        selected:[],
        selected2:[]
      }
    },
    methods:{
        /* Style the row as needed */
      nextPage(){
         if( (this.currentPage * this.perPage) < this.listFilter.length) 
         this.currentPage++
      },
      prePage(){
        if(this.currentPage > 1)
          this.currentPage--
      },
    },
    computed:{
      listFilter () {
        let text = this.search.trim()
        let text2 = this.search2.trim()
        return this.items.filter(item => {
            return item.name.includes(text) && item.sid.includes(text2)
        })
      },
      rows(){
        return this.listFilter.length
      },
      
    },
    async mounted() {
      await this.$store.dispatch('getAllMember')
      this.items = this.$store.getters.getAllFriend
    },
    beforeDestroy(){
      this.$store.commit('setFriendAll','')
    }
}
