import Menu from '@/components/staff/menu'
export default {
  components:{
    Menu
  },
  data(){
      return{
      perPage: 5,
      currentPage: 1,        
      fields: [
        {
          key:'selected',
          label :'selected'
        },
      {
        key: 'sid',
        label: 'รหัสนักศึกษา',
        sortable: true,
      },
      {
        key: 'name',
        label: 'ชื่อเล่น',
      },
      {
        key: 'coin',
        label: 'Coin',
        sortable: true,
      },
      {
          key:'uid',
          label: '*',
      }],items:[],
      item:[],
      search:'',
      search2:'',
      selected:[],
      listFilt:[],
      number:0,
      uid:'',
      index:0,
      s:0,
      loadedArray : []
    }
  },
  watch:{
  },
  methods:{
    set(uid,index){
      this.uid = uid;
      this.index = index
    },
    async addCoin(){
      this.s = 2
    },
    async DiscountCoin(){
      
      this.s = 2
    },
    rowClicked(item) {
      if(item.selected){      
        this.$set(item, 'selected', false)
      }else{
        this.$set(item, 'selected', true)
      }
    },
    nextPage(){
        if( (this.currentPage * this.perPage) < this.listFilter.length) 
        this.currentPage++
    },
    prePage(){
      if(this.currentPage > 1)
        this.currentPage--
    },
    onRowSelected (value, index) {
      if(this.selected.length > 0)
        if (!this.selected.includes(value)) {
            this.selected.push(value)
        }
        else{
          let x = this.selected.findIndex(each => each == value)
          this.selected.splice(x, 1)
        }
      else
        this.selected.push(value)
    },
    test(item){
        if(this.selected)
          if (this.selected.includes(item))
          {
            return true;
            }
          return false;
        return false
    },
    async DeleteFromGroup(){
        await this.$store.dispatch('DeletMemberFromGroup',this.selected)
        this.items = this.$store.getters.getGroup
        this.selected = []
        this.s = 2
    }
  },

  computed:{
    listFilter () {
      let k = this.s
      this.s=0
      let text = this.search.trim()
      let text2 = this.search2.trim()
      if (this.items.length > 0)
      return this.items.filter(item => {
          return item.name.includes(text) && item.sid.includes(text2)
      })
      else
      return ''
    },
    rows(){
      return this.listFilter.length
    },
    h(){
      return this.items = this.$store.getters.getAllFriend
    },
  },
  async mounted() {
    this.item = await this.$store.getters.getAllFriend
    if(this.item.length == 0)
      await this.$store.dispatch('getAllMember')
    await this.$store.dispatch('LoadGroup')
    this.items = this.$store.getters.getGroup
  },
  beforeDestroy(){
    this.$store.commit('setGroup','')
  }
}
